import axios from 'axios';
import { authHeader } from './authservice/auth-header';

const API_URL = process.env.VUE_APP_BASE_API_URL;

class SubscriptionService {
    getSubscription() {
        return axios.get(API_URL+'subscribe', { headers: authHeader() });
    }
    getSubscriptionUnpaid() {
        return axios.get(API_URL+'subscribe/unpaid', { headers: authHeader() });
    }
    getSubscriptionAccept(data) {
        return axios.post(API_URL+'subscribe/accept', data, { headers: authHeader() });
    }
    getSubscriptionDecline(data) {
        return axios.post(API_URL+'subscribe/decline', data, { headers: authHeader() });
    }


}

export default new SubscriptionService();
