<template>
  <Layout>
    <PageHeader :title="title" :items="items" />
      <div class="row mb-4">
      <div class="col-12">
        <div class="card">
          <div class="card-body">
          <div class="row mt-5 mb-5 pb-4 mx-auto" v-if="loading">
                <div class="col-12 p-5 m-4 text-center">
                    <div class="spinner-border text-secondary" role="status" v-if="loading">
                        <span class="sr-only">Loading..</span>
                      </div>
                </div>
            </div>
             <div class="row mb-0" v-if="!loading">
                 <div class="col-5 col-xl-7">
                  <div class="">
                      <b-dropdown class="btn-group mr-2 mb-2 mb-sm-0 border rounded" variant="white">
                        <template slot="button-content">
                          {{ $t('table.show-label') }}
                          <i class="mdi mdi-chevron-down ml-2"></i>
                        </template>
                        <span v-for="val in pageOptions" :key="val">
                             <button class="dropdown-item" @click="perPageSelect(val)">
                             <span class="text-success" v-if="val == perPage"><b> {{val}} </b></span>
                             <span v-else>{{val}}</span>
                            </button>
                        </span>
                      </b-dropdown>

                      <b-button class="btn-group py-2 mr-2 mb-2 mb-sm-0 border"  @click="getSubscriptionsUpdate()" :variant="this.paid ? 'primary' : 'white'">
                          {{ $t('subscriptions.paid-cancel') }}
                      </b-button>
                       <b-button class="btn-group py-2 mr-2 mb-2 mb-sm-0 border" variant="white"  @click="unpaid()" :class="this.paid ? 'btn-white' : 'btn-primary' ">
                          {{ $t('subscriptions.unpaid') }}
                      </b-button>
                      <!-- <b-dropdown class="btn-group mr-2 mb-2 mb-sm-0 border" variant="white" v-if="selected[0]">
                        <template slot="button-content" v-if="user_type == 'su' || user_type == 'adm'">
                          {{ $t('button.bulk-action') }}
                          <i class="mdi mdi-chevron-down ml-2"></i>
                        </template>
                            <button class="dropdown-item" @click="bulkAction()"> {{ $t('button.hapus.text') }}</button>
                      </b-dropdown> -->
                  </div>
              </div>
              <div class="col-4 col-xl-3">
              </div>
                <div class="col-3 col-xl-2">
                  <div class="form-group has-search">
                    <span class="bx bx-search-alt search-icon form-control-feedback"></span>
                      <b-form-input
                      v-model="filter"
                      type="search"
                      :placeholder="$t('navbar.search.text')"
                      class="form-control"
                    ></b-form-input>
                  </div>
              </div>
              <!-- end col-->
             </div>
            <!-- Table -->
            <div class="table-responsive mb-0" :class="{ 'pb-5' : rows < 10 }">
              <div v-if="!loading">
              <b-table
                :items="tableData"
                :fields="fields"
                bordered
                striped
                responsive="sm"
                :per-page="perPage"
                :current-page="currentPage"
                :sort-desc.sync="sortDesc"
                :filter="filter"
                :filter-included-fields="filterOn"
                @filtered="onFiltered"
              >
                 <template #head(bulk) width="4%">
                    <b-form-checkbox
                            v-model="selectAll" 
                            @change="select"
                            class="text-center mt-0">
                    </b-form-checkbox>
                  </template>
                  <template #cell(bulk)="row" width="4%">
                  <b-form-checkbox
                      class="text-center mt-0"
                          v-model="selected"
                          :value="row.item.id_subscription"
                        >
                      </b-form-checkbox>
                </template>

                <template #cell(customer)="row">
                      <span v-if="row.item.customer">{{row.item.customer.name}}</span>
                      <span v-else>-</span>
                </template>


                <template #cell(package)="row">
                      <span v-if="row.item.package">{{row.item.package.package_name}}</span>
                      <span v-else>-</span>
                </template>

                <template #cell(payment)="row">
                      <span v-if="row.item.payment">{{row.item.payment.payment_bank}}</span>
                      <span v-else>-</span>
                </template>

                 <template #cell(total_price)="row">
                      <span>{{formatPrice(row.item.total_price)}}</span>
                </template>

                 <template #cell(payment_status)="row">
                      <span class="badge-pill badge-soft-info px-2 py-1 rounded" v-if="row.item.payment_status == 'unpaid'">
                          {{row.item.payment_status}}
                      </span>
                      <span class="badge-pill badge-soft-danger px-2 py-1 rounded" v-if="row.item.payment_status == 'cancel'">
                          {{row.item.payment_status}}
                      </span>
                      <span class="badge-pill badge-soft-success px-2 py-1 rounded"  v-if="row.item.payment_status == 'paid'">
                          {{row.item.payment_status}}
                      </span>
                </template>
                <template #cell(actions)="row">
                   <div v-if="paid">
                     <b-button class="btn btn-info btn-sm mr-1" @click="modalDesc(row.item.id_subscription)"> {{$t('button.view-detail')}} </b-button>
                        <b-modal
                        ref="ref-modal-edit"
                        :id="'modal-desc' + row.item.id_subscription"
                        centered
                        title="Detail"
                        title-class="font-17"
                        :ok-title="$t('button.update.text')"
                        :cancel-title="$t('button.cancel.text')"
                      >
                            <!-- <p class="mb-2">
                              Id Subscribtions:
                              <span class="text-primary"><b> #{{row.item.id_subscription}} </b></span>
                            </p> -->
                            <p class="mb-4" v-if="row.item.customer">
                             <b>{{$t('user-management.customer')}} </b>  <br>
                             <span>{{$t('user-management.name')}} : {{row.item.customer.name}} </span><br>
                             <span>{{$t('user-management.email')}} :  {{row.item.customer.email}}</span><br>
                             <span>{{$t('user-management.phone')}} :  {{row.item.customer.phone}}</span>
                              <span class="text-primary"></span>
                            </p>
                            <div class="table-responsive">
                              <table class="table table-centered table-nowrap table-bordered">
                                <thead>
                                  <tr>
                                    <td width="30%">{{$t('package.package-name')}} </td>
                                    <td scope="col">{{row.item.package.package_name}}</td>
                                  </tr>
                                </thead>
                                <tbody>
                                  <tr>
                                    <td width="30%">{{$t('package.duration')}}</td>
                                    <td scope="col">{{row.item.package.package_duration}} ( {{row.item.package.unit}} )</td>
                                  </tr>
                                  <tr>
                                    <td width="30%">{{$t('package.description')}}</td>
                                    <td scope="col">{{row.item.package.package_desc}}</td>
                                  </tr>
                                  <br>
                                   <tr>
                                    <td width="30%">{{$t('package.price')}}</td>
                                    <td scope="col" class="text-right">{{formatPrice(row.item.package.price)}}</td>
                                  </tr>
                                  <tr>
                                    <td width="30%">{{$t('payment.payment-code')}}</td>
                                    <td scope="col" class="text-right">{{row.item.payment_code}}</td>
                                  </tr>
                                  <tr>
                                    <th>
                                      <h6 class="m-0">{{$t('payment.payment-total')}}</h6>
                                    </th>
                                    <td class="text-right"><b>{{formatPrice(row.item.total_price)}}</b></td>
                                  </tr>
                                  
                                  <br>
                                  <tr>
                                    <td colspan="2">
                                      <span class="m-0 text-left">{{$t('payment.payment-bank')}}</span><br>
                                      <b>{{row.item.payment.payment_bank}}</b><br>
                                      <span>{{row.item.payment.payment_account_number}}</span>
                                    </td>
                                  </tr>
                                </tbody>
                              </table>
                            </div>
                            <template v-slot:modal-footer>
                              <b-button variant="secondary" @click="closeModal(row.item.id_subscription)">Close</b-button>
                            </template>
                      </b-modal>
                   </div>
                   <div v-else>
                      <b-button class="btn mr-1" variant="success" size="sm" @click="accept(row.item.id_subscription)">
                          {{$t('subscriptions.paid')}}
                      </b-button>
                      <b-button class="btn" variant="danger" size="sm"  @click="cancel(row.item.id_subscription)">
                          {{$t('subscriptions.cancel')}}
                      </b-button>
                   </div>
                </template>
              </b-table>
            </div>
            <div class="row" v-if="rows > 10">
              <div class="col">
                <div
                  class="dataTables_paginate paging_simple_numbers float-right"
                >
                  <ul class="pagination pagination-rounded mb-0">
                    <!-- pagination -->
                    <b-pagination
                      v-model="currentPage"
                      :total-rows="rows"
                      :per-page="perPage"
                    ></b-pagination>
                  </ul>
                </div>
              </div>
            </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </Layout>
</template>

<script>
import Vue from "vue";
import Swal from "sweetalert2";
import Layout from "../../layouts/main";
import PageHeader from "@/components/page-header";
import appConfig from "@/app.config";
import { required, sameAs } from "vuelidate/lib/validators";
import UserService from "@/helpers/user.service";
import SubscriptionService from "@/helpers/subscription.service";
import store from '@/state/store';
import moment from "moment";
// import NavBar from "@/components/nav-bar.vue";

export default {
  page: {
    meta: [{ name: "description", content: appConfig.description }],
  },
  components: { Layout, PageHeader },
  data() {
    return {
      urlImage: process.env.VUE_APP_BASE_API_URL_AVATAR,
      user_type: store.state.authBackend.user.type,
      title: this.$t("subscriptions.subscriptions-management"),
      items: [
        {
          text: this.$t("menuitems.dashboards.text"),
          href: "#",
        },
        {
          text: this.$t("subscriptions.subscriptions-management"),
          active: true,
        },
      ],
      paid:true,
      loading_all:false,
      loading:false,
      tableData:[],
      notif_total:'',
      all: true,
      totalRows: 1,
      currentPage: 1,
      perPage: 10,
      pageOptions: [10, 25, 50, 100],
      filter: null,
      filterOn: [],
      sortDesc: false,
      selected:[],
      selectAll: false,
      fields: [
        { key: "id_subscription", thClass: "d-none", tdClass: "d-none" },
        { key: "bulk",  thStyle: { width: "2%" }, label:'#'},
        {
          key: "customer",
          sortable: true,
          label: this.$t("subscriptions.customer"),

        },
        { key: "avatar", thClass: "d-none", tdClass: "d-none" },
        {
          key: "package",
          sortable: true,
          label: this.$t("subscriptions.package"),
        },
        {
          key: "payment",
          sortable: true,
          thStyle: { width: "10%" },
          label: this.$t("subscriptions.bank"),
        },
        {
          key: "total_price",
          sortable: true,
          thStyle: { width: "15%" },
          label: this.$t("subscriptions.total-payment"),
        },
        {
          key: "payment_status",
          sortable: true,
          thStyle: { width: "15%" },
          label: this.$t("subscriptions.payment-status"),
        },
        {
          key: "actions",
          class: "text-center",
          thStyle: { width: "15%" },
          label: this.$t("button.action"),
        },
      ],
      submitted: false,
    };
  },
  validations: {
    form: {
        region_name: { required },
        region_number: { required }
    },
  },
  computed: {
    rows() {
      return this.tableData.length;
    },
  },
  methods: {
    list() {
      // this.loading = true;
      UserService.getProfile()
      .then(response => {
        // console.log( response.data.data)
        this.user_type = response.data.data.admin.role
      })
      .catch(e => {
        this.message = e.response.data.data
      });
    },

    select() {
        this.selected = [];
        if (!this.selectAll) {
            for (let i in this.tableData) {
                this.selected.push(this.tableData[i].id_region);
            }
        }
    },

    modalDesc(id_subscription) {
      this.$bvModal.show("modal-desc" + id_subscription);
    },

    closeModal(id_subscription){
       this.$bvModal.hide("modal-desc" + id_subscription);
    },

    getSubscriptions() {
      this.loading = true;
      SubscriptionService.getSubscription()
        .then((response) => {
          this.tableData = [];
          // console.log(response.data.data);
          this.notif_total = response.data.data.length;
          response.data.data.forEach((val) => {
            const data = {
              id_subscription : val.id_subscription,
              id_customer: val.id_customer,
              id_package: val.id_package,
              package: val.package,
              customer: val.account,
              payment_status: val.payment_status,
              payment: val.payment,
              payment_id: val.payment_id,
              payment_code: val.payment_code,
              total_price: val.total_price,
              created_at: this.formatDate(val.created_at),
              updated_at: this.formatDate(val.updated_at),
            };
            this.tableData.push(data);
          });
          this.loading = false;
        })
        .catch((err) => {
          this.loading = false;
          console.log(err);
        });
    },

    getSubscriptionsUpdate() {
      this.paid = true;
      SubscriptionService.getSubscription()
        .then((response) => {
          this.tableData = [];
          // console.log(response.data.data);
          this.notif_total = response.data.data.length;
          response.data.data.forEach((val) => {
            const data = {
              id_subscription : val.id_subscription,
              id_customer: val.id_customer,
              id_package: val.id_package,
              package: val.package,
              customer: val.account,
              payment_status: val.payment_status,
              payment: val.payment,
              payment_id: val.payment_id,
              payment_code: val.payment_code,
              total_price: val.total_price,
              created_at: this.formatDate(val.created_at),
              updated_at: this.formatDate(val.updated_at),
            };
            this.tableData.push(data);
          });
        })
        .catch((err) => {
          console.log(err);
        });
    },

    unpaid() {
      this.paid = false;
      SubscriptionService.getSubscriptionUnpaid()
        .then((response) => {
          this.tableData = [];
          // console.log(response.data.data);
          this.notif_total = response.data.data.length;
          response.data.data.forEach((val) => {
            const data = {
              id_subscription : val.id_subscription,
              id_customer: val.id_customer,
              id_package: val.id_package,
              package: val.package,
              customer: val.account,
              payment_status: val.payment_status,
              payment: val.payment,
              payment_id: val.payment_id,
              payment_code: val.payment_code,
              total_price: val.total_price,
              created_at: this.formatDate(val.created_at),
              updated_at: this.formatDate(val.updated_at),
            };
            this.tableData.push(data);
          });
        })
        .catch((err) => {
          console.log(err);
        });
    },

    accept(id) {
      this.paid = true;
      let data = {
                "id_subscription" : id
            }
      console.log(data);
      SubscriptionService.getSubscriptionAccept(data)
        .then((response) => {
          this.tableData = [];
          // console.log(response.data.data);
          this.getSubscriptionsUpdate();
        })
        .catch((err) => {
          console.log(err);
        });
    },

    cancel(id){
      this.paid = true;
      let data = {
                "id_subscription" : id
            }
      console.log(data);
      SubscriptionService.getSubscriptionDecline(data)
        .then((response) => {
          this.tableData = [];
          // console.log(response.data.data);
          this.getSubscriptionsUpdate();
        })
        .catch((err) => {
          console.log(err);
        });
    },

    onFiltered(filteredItems) {
      this.totalRows = filteredItems.length;
      this.currentPage = 1;
    },
    
    perPageSelect(val){
      this.perPage = val;
      console.log(this.perPage);
      this.$refs.dropdown.hide();
    },

    formatDate(value) {
      return moment(value).format("YYYY-MM-DD, hh:mm");
    },

    formatPrice(value) {
        return value.toString().replace(/(\d)(?=(\d{3})+(?:\.\d+)?$)/g, "$1\.")
    },

  },
  beforeMount(){
    this.list();
  },
  mounted(){
    this.getSubscriptions();
  }
};
</script>

<style scoped>
.badge {
  border-radius: 2%;
}
#my-radio {
	accent-color: #174F80;
}

</style>
